.only-dark-mode {
  display: none;
}

.hi-dark-mode .only-light-mode {
  display: none;
}
.hi-dark-mode .only-dark-mode {
  display: inline-block;
}

html {
  font-size: 14;
}

body {
  font-family: "Outfit", sans-serif;
  background-color: #fff;
  overflow: hidden;
  transition: display ease 200ms;
  color: #222222;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #111111;
    color: #f4f8fc;
  }

  body.hi-light-mode {
    background-color: #fff;
    color: #222222;
  }
}
@media (prefers-color-scheme: light) {
  body.hi-dark-mode {
    background-color: #111111;
    color: #f4f8fc;
  }
}
.hi-layout {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  width: 100vw;
  height: 100vh;
  transition: all 200ms ease;
}
@media screen and (min-width: 768px) {
  .hi-layout {
    grid-template-columns: 60px 1fr;
  }
}
@media screen and (min-width: 992px) {
  .hi-layout {
    grid-template-columns: 250px 1fr;
  }
}
.hi-layout__main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px 1fr;
  overflow: hidden;
}
.hi-layout__content {
  overflow-y: auto;
}
.hi-layout--scrollable {
  overflow-y: auto;
}

.hi-layout__topbar {
  display: flex;
  transition: all 200ms ease;
  display: flex;
  background-color: #fff;
}
.hi-layout__topbar-section {
  display: flex;
  flex: 1;
  align-items: center;
  padding: 0 2rem;
}
.hi-layout__offcanvas-trigger {
  display: flex;
  justify-content: flex-end;
  font-size: 1.4rem;
  cursor: pointer;
}

@media (prefers-color-scheme: dark) {
  .hi-layout__topbar {
    background-color: #111111;
  }

  .hi-light-mode .hi-layout__topbar {
    background-color: #fff;
  }
}
@media (prefers-color-scheme: light) {
  .hi-dark-mode .hi-layout__topbar {
    background-color: #111111;
  }
}
.hi-layout__sidebar {
  background-color: #f4345f;
  grid-template-rows: 60px 60px 1fr;
  grid-template-columns: 1fr;
  display: none;
}
@media screen and (min-width: 768px) {
  .hi-layout__sidebar {
    display: grid;
  }
}
.hi-layout__sidebar-oc-trigger {
  display: flex;
  align-items: center;
  height: inherit;
  justify-content: center;
  font-size: 1.2rem;
  cursor: pointer;
  color: #f4f8fc;
}
.hi-layout__logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.hi-layout__logo img {
  max-height: 50px;
}
.hi-layout__logo-full {
  display: none;
}

@media (prefers-color-scheme: dark) {
  .hi-layout__sidebar {
    background-color: #161616;
  }

  .hi-light-mode .hi-layout__sidebar {
    background-color: #f4345f;
  }
}
@media (prefers-color-scheme: light) {
  .hi-dark-mode .hi-layout__sidebar {
    background-color: #161616;
  }
}
.hi-navigation {
  display: none;
}
@media screen and (min-width: 992px) {
  .hi-navigation {
    display: block;
  }
}
.hi-navigation__item {
  text-decoration: none;
  display: flex;
  height: 40px;
  align-items: center;
  color: #fff;
  position: relative;
  cursor: pointer !important;
  padding: 0 20px;
  justify-content: flex-start;
}
.hi-navigation__item:hover {
  color: #e6e6e6;
}
.hi-navigation__item[data-bs-toggle=collapse]::after {
  font-family: bootstrap-icons !important;
  content: "\f282";
  position: absolute;
  right: 20px;
  font-size: 0.7rem;
  height: 100%;
  display: flex;
  align-items: center;
}
.hi-navigation__item[data-bs-toggle=collapse]:not(.collapsed) {
  background: rgba(0, 0, 0, 0.1);
}
.hi-navigation__item:not(.collapsed)::after {
  transform: rotate(180deg);
}
.hi-navigation__item--sub {
  height: 35px;
  padding-left: 0.1rem;
  font-size: 0.9rem;
  padding-left: 2rem;
}
.hi-navigation__item--sub::before {
  content: "•  ";
}
.hi-navigation__item--sub::after {
  content: "";
}
.hi-navigation__item--sub:hover {
  background: none;
  font-weight: 700;
}
.hi-navigation__item--active {
  font-weight: 700 !important;
}
.hi-navigation__icon {
  font-size: 1.3rem;
}
@media screen and (min-width: 992px) {
  .hi-navigation__icon {
    font-size: 1rem;
    margin-right: 10px;
  }
}
.hi-navigation__title {
  display: none;
  transition: all 200ms ease;
  margin-top: -2px;
}
@media screen and (min-width: 992px) {
  .hi-navigation__title {
    display: block;
  }
}

.hi-offcanvas .hi-navigation {
  display: block;
}
.hi-offcanvas .hi-navigation__title {
  display: block;
}
.hi-offcanvas .hi-navigation__item {
  justify-content: flex-start;
}
.hi-offcanvas .hi-navigation__icon {
  font-size: 1rem;
  margin-right: 10px;
}

.hi-header {
  background-color: #f4f8fc;
  padding: 2rem 2rem;
  position: relative;
}
.hi-header__title {
  margin-bottom: 0;
}
.hi-header__subtitle {
  margin: 0;
}
.hi-header--collapsed {
  padding: 1em;
}
.hi-header--collapsed .hi-header__subtitle {
  display: none;
}
.hi-header--centered {
  text-align: center;
}
.hi-header--right {
  text-align: right;
}

@media (prefers-color-scheme: dark) {
  .hi-header {
    background-color: #111111;
  }

  .hi-light-mode .hi-header {
    background-color: #f4f8fc;
  }
}
@media (prefers-color-scheme: light) {
  .hi-dark-mode .hi-header {
    background-color: #111111;
  }
}
.hi-yield {
  padding: 2rem 2rem;
}

.hi-section {
  display: flex;
  padding: 1rem 2rem;
  flex-direction: column;
  width: 100%;
}
.hi-section--hfull {
  height: 100vh;
}
.hi-section--vcentered {
  justify-content: center;
}
.hi-section--hcentered {
  align-items: center;
}

.hi-offcanvas {
  background: #f4345f;
  border: none;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.hi-offcanvas .offcanvas-header {
  color: #f4f8fc;
}
.hi-offcanvas .offcanvas-body {
  padding: 0;
}
.hi-offcanvas__close {
  color: white;
  background: none;
  text-decoration: none;
}
.hi-offcanvas__close::before {
  font-family: "bootstrap-icons";
  content: "\f62a";
  width: 10px;
  height: 10px;
  cursor: pointer;
}

@media (prefers-color-scheme: dark) {
  .hi-offcanvas {
    background: #111111;
    border-right: 1px solid #070707;
    box-shadow: rgba(0, 0, 0, 0.04) 5px 5px 5px;
  }

  .hi-light-mode .hi-offcanvas {
    background: #f4345f;
    border: none;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  }
}
@media (prefers-color-scheme: light) {
  .hi-dark-mode .hi-offcanvas {
    background: #111111;
    border-right: 1px solid #070707;
    box-shadow: rgba(0, 0, 0, 0.04) 5px 5px 5px;
  }
}
.hi-code {
  padding: 0 2rem;
  background-color: #0c0c0c;
  color: #fff;
  font-family: "PT Mono", monospace;
  font-size: 0.9rem;
}
.hi-code xmp {
  margin: 0;
  color: #fff;
}
.hi-code pre {
  margin: 0;
  padding: 0;
}
.hi-code .hljs,
.hi-code .hljs-attr {
  background: none;
  color: #fff;
}
.hi-code .hljs-string {
  color: #d2f0a6;
}
.hi-code .hljs-attr {
  color: #f5a2a9;
}
.hi-code .hljs-name {
  color: #f5a2a9;
}
.hi-code .hljs-tag {
  color: #f5a2a9;
}

.hi-btn {
  text-transform: uppercase;
  padding-left: 1em;
  padding-right: 1em;
  font-weight: 700;
  border-width: 2.5px;
  border-radius: 0.1rem;
  display: inline-flex;
  justify-content: center;
  text-align: center;
}
.hi-btn i, .hi-btn__icon {
  margin-right: 0.3rem;
}
.hi-btn--rounded {
  border-radius: 5rem;
}
.hi-btn--icon-only i, .hi-btn--icon-only .hi-btn__icon {
  margin: 0;
}

.hi-toolbar {
  display: flex;
  justify-content: flex-start;
}
.hi-toolbar--centered {
  justify-content: center;
}
.hi-toolbar--right {
  justify-content: flex-end;
}
.hi-toolbar-wrapper {
  display: flex;
  height: 45px;
  display: inline-flex;
  background: none;
  overflow: hidden;
  border-radius: 4px;
}
.hi-toolbar__item {
  min-width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none;
  position: relative;
}
.hi-toolbar__item::before {
  height: 50%;
  width: 1px;
  background-color: rgba(0, 0, 0, 0.04);
  content: "";
  position: absolute;
  left: 0;
}
.hi-toolbar__item:first-child::before {
  width: 0;
  height: 0;
}
.hi-toolbar__item:hover {
  background: rgba(0, 0, 0, 0.02);
}
.hi-toolbar__item--active {
  background: #f4345f;
  color: #222222;
}
.hi-toolbar--fluid {
  width: 100%;
}
.hi-toolbar--fluid .hi-toolbar-wrapper {
  width: 100%;
}
.hi-toolbar--fluid .hi-toolbar__item {
  flex: 1;
}
.hi-toolbar--slim .hi-toolbar-wrapper {
  height: 30px;
  font-size: 0.9em;
}

@media (prefers-color-scheme: dark) {
  .hi-toolbar__item::before {
    background-color: rgba(255, 255, 255, 0.04);
  }
  .hi-toolbar__item:hover {
    background: rgba(255, 255, 255, 0.02);
  }

  .hi-light-mode .hi-toolbar__item::before {
    background-color: rgba(0, 0, 0, 0.04);
  }
  .hi-light-mode .hi-toolbar__item:hover {
    background: rgba(0, 0, 0, 0.02);
  }
}
@media (prefers-color-scheme: light) {
  .hi-light-mode .hi-toolbar__item::before {
    background-color: rgba(255, 255, 255, 0.04);
  }
  .hi-light-mode .hi-toolbar__item:hover {
    background: rgba(255, 255, 255, 0.02);
  }
}
.hi-table {
  background: white;
  width: 100%;
  border-radius: 4px;
  overflow-x: scroll;
  white-space: nowrap;
  padding-bottom: 1rem;
}
.hi-table table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
}
.hi-table thead {
  border-collapse: collapse;
  background-color: #f8f8f9;
}
.hi-table thead tr:hover {
  background-color: #f8f8f9;
}
.hi-table th {
  text-align: left;
  padding: 0.9rem 1rem;
  text-transform: uppercase;
  font-size: 0.7rem;
  white-space: nowrap;
}
.hi-table tr, .hi-table__tr {
  border-bottom: 1px solid #f4f4f4;
}
.hi-table tr:last-child, .hi-table__tr:last-child {
  border-bottom: none;
}
.hi-table tr:nth-child(even), .hi-table__tr:nth-child(even) {
  background-color: #f4f8fc;
}
.hi-table tr:hover, .hi-table__tr:hover {
  background: #fff5e8;
  color: #222222;
  cursor: pointer;
}
.hi-table tr--selected, .hi-table__tr--selected {
  background: #ffeed9 !important;
  color: #222222;
  cursor: pointer;
}
.hi-table tr td, .hi-table__tr td {
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
}
.hi-table__meta {
  display: flex;
  height: 40px;
  align-items: center;
  padding: 0 1rem;
  margin-bottom: 1rem;
  justify-content: flex-end;
}

@media (prefers-color-scheme: dark) {
  .hi-table {
    background: none;
  }
  .hi-table thead {
    background-color: #161616;
  }
  .hi-table thead tr:hover {
    background-color: #161616;
    color: #f4f8fc;
  }
  .hi-table tr {
    border-bottom: #1f1e26;
  }
  .hi-table tr:nth-child(even) {
    background-color: #161616;
  }
  .hi-table tr:hover {
    background: #1f1e26;
    background: #fff5e8;
    cursor: pointer;
  }
  .hi-table tr td {
    padding: 0.5rem 1rem;
  }

  .hi-light-mode .hi-table {
    background: #fff;
  }
  .hi-light-mode .hi-table thead {
    background-color: #f8f8f9;
  }
  .hi-light-mode .hi-table thead tr:hover {
    background-color: #f8f8f9;
    color: #222222;
  }
  .hi-light-mode .hi-table tr {
    border-bottom: 1px solid #f4f4f4;
  }
  .hi-light-mode .hi-table tr:nth-child(even) {
    background-color: #f4f8fc;
  }
  .hi-light-mode .hi-table tr:hover {
    background: #fff5e8;
  }
}
@media (prefers-color-scheme: light) {
  .hi-dark-mode .hi-table {
    background: none;
  }
  .hi-dark-mode .hi-table thead {
    background-color: #161616;
  }
  .hi-dark-mode .hi-table thead tr:hover {
    background-color: #161616;
    color: #f4f8fc;
  }
  .hi-dark-mode .hi-table tr {
    border-bottom: #1f1e26;
  }
  .hi-dark-mode .hi-table tr:nth-child(even) {
    background-color: #161616;
  }
  .hi-dark-mode .hi-table tr:hover {
    background: #1f1e26;
    background: #fff5e8;
    cursor: pointer;
  }
  .hi-dark-mode .hi-table tr td {
    padding: 0.5rem 1rem;
  }
}
/* Responsive */
@media only screen and (max-width: 767px) {
  .hi-table {
    width: 100%;
  }
  .hi-table thead {
    display: none;
  }
  .hi-table th, .hi-table td {
    display: flex;
    align-items: center;
    justify-content: left;
  }
  .hi-table tr:first-child {
    border-top: 1px solid #f4f4f4;
    border-top: none;
  }
  .hi-table tr:last-child {
    border-bottom: none;
  }
  .hi-table tr:nth-child(even) {
    background: #f4f8fc;
  }
  .hi-table td {
    font-weight: 400 !important;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .hi-table td::before {
    content: attr(data-col);
    width: 100%;
    font-weight: 700;
  }
}
.hi-card {
  border-radius: 0.5rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border: 1px solid #f4f8fc;
  background: #fff;
  overflow: hidden;
}
.hi-card__main {
  padding: 1rem;
}
.hi-card__header {
  padding: 1rem;
  display: flex;
  border-bottom: 1px solid #f4f8fc;
}
.hi-card__title {
  flex: 1;
  font-weight: 700;
  display: flex;
  align-items: center;
}
.hi-card__menu {
  flex: 1;
