.hi-card-article {
  border-radius: 0.5rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border: 1px solid #f4f8fc;
  background: #fff;
  overflow: hidden;
}
.hi-card-article__main {
  padding: 1rem;
}
.hi-card-article__main {
