.dropdown-menu,
.dropdown-divider {
  border-color: #f4f8fc;
}

.dropdown-menu {
  border: none;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  color: #222222;
}

.dropdown-item:hover {
  background-color: #fff5e8;
}
.dropdown-item i {
  margin-right: 0.5rem;
}
.dropdown-item:focus, .dropdown-item:active {
  background-color: #ffeed9;
  color: #222222;
}

@media (prefers-color-scheme: dark) {
  .dropdown-menu {
    box-shadow: rgba(0, 0, 0, 0.04) 5px 5px 5px;
    color: #f4f8fc;
    background: #161616;
  }

  .dropdown-item {
    color: #f4f8fc;
  }
  .dropdown-item:hover {
    color: #222222;
    background-color: #fff5e8;
  }

  .dropdown-divider {
